export default {
  data () {
    return {
      // tab 栏数据
      tabsList: [{
        text: '已完成',
        id: 0
      },
      {
        text: '待出行',
        id: 1
      },
      {
        text: '待支付',
        id: 2
      },
      {
        text: '退款单',
        id: 3
      },
      ],
      // 默认选中项
      active: 0,
      // 控制有内容和没有内容页面之间的切换显示
      flag: true
    }
  },
  methods: {
    // 点击 tab 栏时 触发
    tabsChange (id) {
      this.active = id
    },
    // 去订单详情页面
    goOrderDetails () {
      uni.navigateTo({
        url: '/pages/orderDetails/orderDetails'
      });
    },
    // 去支付页面
    goPaymentOrder () {
      uni.navigateTo({
        url: '/pages/paymentOrder/paymentOrder'
      });
    },
    // 去我的行程
    goTrip () {
      uni.navigateTo({
        url: '/pages/trip/trip'
      });
    },
    // 去退款页面
    refund () {
      uni.navigateTo({
        url: '/pages/refund/refund'
      });
    }
  }
}
